@import url(https://fonts.googleapis.com/css2?family=Barlow:ital@0;1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital@0;1&display=swap);
:root
{
  --main-color:#76D7C4;
  --text-colorb:black;
  --text-colorw:white;
}


body {
  margin: 0;
  font-family: 'Barlow', sans-serif;
}

code {
  border-radius:10px;
  font-family: 'Roboto Mono', monospace;
  font-size:1.5rem;
  text-align:center;
  background-color:black;
  padding:20px;
  color:white;
}

.tag
{
  color:#85C1E9;
  font-style:italic;
}

.main
{
  position:absolute;
  display:flex;
  flex-direction:column;
  align-items: center;
  justify-content:center;
  box-sizing: border-box;
  width: 100%;
  height:100%;
  padding:0 20px;
  margin-bottom:20px;
  background:url("/portfolio/static/media/wallpaper 2.374dc941.jpg");
  background-position: center;
  background-size:cover;
  overflow: hidden;
}
/* 
.main img
{
  position: absolute;
  z-index:-1;
} */

.main #india
{
  background:linear-gradient(to bottom, orange 0%, white 60%, green 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main #socialcards
{
  border-radius:10px;
  background-color:#76D7C4;
  background-color:var(--main-color);
  margin-top:10px;
  padding:15px 10px;
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  display:flex;
  align-items: center;
  justify-content:space-evenly; 
}


.main #socialcards .card-social
{
  border-radius:50%;
  margin:0 7.5px;
  display:block;
  padding:10px;
  height:40px;
  width:40px;
  background-color:white;
  display:flex;
  align-items: center;
  justify-content:center;
  cursor:pointer;
  box-shadow: 0 3px 8px -2px #2E4053;
}

.main #socialcards .card-social img
{
  height:30px;
}

.highlight
{
  color:#76D7C4;
  color:var(--main-color);
}

#otherarea
{
  box-sizing: border-box;
  position: absolute;
  width:100%;
  top:100%;
  display:flex;
  flex-direction:column;
  align-items:center;
  padding-top:20px;
}

#otherarea .label {
  /* text-align: center; */
  width:90%;
  font-size:1.7rem;
  margin:40px 0 20px 0;
  padding-bottom:10px;
  border-bottom:1px solid #76D7C4;
  border-bottom:1px solid var(--main-color);
}

#otherarea #about
{
  box-sizing: border-box;
  width:90%;
}

#otherarea #about #main-content
{
  width:100%;
  display:flex;
  align-items:flex-start;
  justify-content: center;
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
}

#otherarea #about #main-content .imgholder
{
  margin-right:10px;
  border-radius:10px;
  display:flex;
  align-items: center;
  justify-content:center;
  height:150px;
  width:150px;
  overflow: hidden;
  box-shadow: 0 3px 8px -2px #2E4053;
}

#otherarea #about #main-content .imgholder img
{
  height:100%;
}


#otherarea #about #main-content .content
{
  font-size:1.3rem;
  border-radius:10px;
  text-align:justify;
  display:inline-block;
  box-sizing:border-box;
  padding:20px 25px;
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
  box-sizing:border-box;
  line-height:2.5rem;
  width:75%;
  box-shadow: 0 3px 8px -2px #2E4053;
}

#otherarea #footer {
  box-sizing: border-box;
  width:100%;
  display:flex;
  flex-direction:column;
  align-items: center;
  justify-content:center;
  text-align:center;
  padding:20px;
  font-size:1.2rem;
}

#otherarea #footer span
{
  margin:5px;
  display:flex;
  align-items: center;
  justify-content:center;
}

#otherarea #footer img {
  height:25px;
}



@media screen and (max-width:769px)
{
  #otherarea .label
  {
    font-size:1.4rem;
  }

  .main #socialcards .card-social
  {
    margin:0 5px;
    height:30px;
    width:30px;
  }

  #otherarea #about #main-content .content
  {
  font-size:1.2rem;
  }
}

@media screen and (max-width:575px)
{
  code {
    font-size:1.4rem;
  }

  #otherarea #about #main-content
  {
    flex-direction: column;
    align-items: center;
  }

  #otherarea #about #main-content .content
  {
    margin-top:20px;
    width:100%;
  }

  #otherarea #footer {
    font-size:1rem;
  }

  #otherarea #footer img {
    height:20px;
  }
}
